* {
  box-sizing: border-box !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

:root {
  font-size: 16px;
  --background-color: #000;
  --text-color: #fff;
  --highlight-blue: #00FFF0;
  --highlight-pink: #FF4EB8;
  --highlight-yellow: #FAFF00;
  --font-titles: "PT Mono";
  --font-body: "Work Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --highlight-gray: rgba(255, 255, 255, 0.22);
  --tn-flag: url('data:image/svg+xml;base64,');
}

html, body {
  padding: 0;
  font-family: var(--font-body);
}

body {
  color: var(--text-color);
  background-color: var(--background-color);
}

h1, h2 {
  font-family: 'Work Sans';
}

